  content: "ON";
  padding-left: 7px;
  background-color: #1ab394;
  color: #ffffff;
}
.onoffswitch-inner:after {
  content: "OFF";
  padding-right: 7px;
  background-color: #ffffff;
  color: #919191;
  text-align: right;
}
.onoffswitch-switch {
  display: block;
